import { Component, ApplicationRef } from '@angular/core';
import { Router } from '@angular/router-deprecated';
import {AuthMediator} from '../services/auth.mediator';

@Component({
  selector: 'login',
  styles: [ require('./login.css') ],
  template: `
  <section class="login-container">
    <md-card>
      <md-card-title>התחברות</md-card-title>
      <md-card-content>
      
          <button class="facebook-login" md-raised-button (click)="facebookLogin()">התחברות דרך facebook</button>
          <p>
          לצורכי אמינות בחרנו ב-facebook בתור כלי ההזדהות ב-hand2hand
</p>
      
      	  <!--<span (click)="facebookLogin()">Facebook</span> -->

        <!--<form (ngSubmit)="onSubmit(loginForm.value)" #loginForm="ngForm">-->
          <!--<md-input required placeholder="Email" ngControl="email" pattern="^.+@.+\\..+$" [(ngModel)]="email">-->
            <!--<md-hint *ngIf="loginForm.controls.email?.errors?.pattern" align="end">זה לא ממש נראה כמו מייל...</md-hint>-->
          <!--</md-input>-->
          <!--<md-input required type="password" placeholder="סיסמה" ngControl="password" [(ngModel)]="password"></md-input>-->
          <!--<input [disabled]="!loginForm.form.valid" type="submit" md-raised-button class="md-primary" value="התחבר"/>-->
        <!--</form>-->
      </md-card-content>
            	  {{ user | json }}

    </md-card>
  </section>
  `
})
export class Login {
  user;

  constructor(private authMediator: AuthMediator,
    private router: Router) { }

  onSubmit({email: email, password: password}) {
    console.log(email, password);
  }

  facebookLogin() {
    this.authMediator.login().subscribe(x => {
      this.router.navigate(['Store']);
    });

    // window.open('http://192.168.1.104:3000/api/auth/facebook','','scrollbars=yes,menubar=no,width=500, resizable=yes,toolbar=no,location=no,status=no');

  }
}



