import { Component } from '@angular/core';
import { ApiProxy } from '../../services';
import { StoreItem } from '../store-item/index';


@Component({
  selector: 'main',
  styles: [ require('./main.css') ],
  template: require('./main.html'),
  directives: [StoreItem]
})
export class Main {
  products;

  constructor(protected proxy: ApiProxy) {

  }

  ngOnInit() {
    this.proxy.getAllProducts()
      .subscribe(products => this.products = products);
  }

  add() {
    this.proxy.updateProduct("AVaU6Zap9Iu6vSdmGx_0", {
      "category": "בגדים",
      "sub_category": "חגורה",
      "price": 151.63,
      "condition": "חדש",
      "description": "חודדה הגבר רצח"
    }).subscribe(x => console.log(x));
  }
}



