import { Component } from '@angular/core';
import { RouteParams } from '@angular/router-deprecated';
import { ApiProxy } from '../../services';
import { StoreItem } from '../store-item/index';


@Component({
  selector: 'main',
  styles: [ require('./product.css') ],
  template: require('./product.html'),
  directives: [StoreItem]
})
export class ProductComponent {
  product;

  constructor(protected proxy: ApiProxy, private routeParams: RouteParams) {

  }

  ngOnInit() {
    let id = this.routeParams.get('id');
    
    this.proxy.getProduct(id)
      .subscribe(product => this.product = product);
  }
}



